import { Component, ComponentFactory, Input, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import { IPlainObject } from '@helix/platform/shared/api';

@Component({
  selector: 'rx-dynamic-component-renderer',
  template: '<ng-container #container></ng-container>'
})
export class RxDynamicComponentRendererComponent implements OnInit {
  @Input()
  componentFactory: ComponentFactory<any>;

  @Input()
  context: any;

  @Input()
  options: IPlainObject;

  @ViewChild('container', { static: true, read: ViewContainerRef })
  container: ViewContainerRef;

  ngOnInit() {
    const componentRef = this.container.createComponent(this.componentFactory);

    Object.assign(componentRef.instance, {
      context: this.context,
      options: this.options
    });
  }
}
